const PaginationNumBers = document.getElementById('is-pagination-numbers');
const PrevBtn = document.getElementById('prev');
const NextBtn = document.getElementById('next');
const TotalPages = 7;
let currentPage = 1;

// ボタンの有効・無効を切り替える関数
const UpdateButtons = () => {
  PrevBtn.disabled = currentPage === 1;
  NextBtn.disabled = currentPage === TotalPages;
};

// ページボタン生成関数
const CreateNumbers = () => {
  PaginationNumBers.innerHTML = "";

  if (TotalPages <= 5) {
    for (let i = 1; i <= TotalPages; i++) {
      CreatePageBtn(i);
    }
  } else {
    if (currentPage <= 3) {
      for (let i = 1; i <= 3; i++) {
        CreatePageBtn(i);
      }
      CreateEllipsis();
      CreatePageBtn(TotalPages);
    } else if (currentPage === 4) {
      CreatePageBtn(1);
      for (let i = 3; i <= 5; i++) {
        CreatePageBtn(i);
      }
      CreateEllipsis();
      CreatePageBtn(TotalPages);
    } else if (currentPage >= TotalPages - 2) {
      CreatePageBtn(1);
      CreateEllipsis();
      for (let i = TotalPages - 2; i <= TotalPages; i++) {
        CreatePageBtn(i);
      }
    } else {
      CreatePageBtn(1);
      CreateEllipsis();
      for (let i = currentPage - 1; i <= currentPage + 1; i++) {
        CreatePageBtn(i);
      }
      CreateEllipsis();
      CreatePageBtn(TotalPages);
    }
  }

  UpdateButtons();
};

// ページボタンを生成する関数
const CreatePageBtn = (i) => {
  const PageBtn = document.createElement("button");
  PageBtn.classList.add("c-page-btn");
  PageBtn.innerText = i;

  if (i === currentPage) {
    PageBtn.classList.add("is-active");
  }

  PageBtn.addEventListener("click", () => {
    currentPage = i;
    CreateNumbers();
  });

  PaginationNumBers.appendChild(PageBtn);
};

// 省略記号(...)を生成する関数
const CreateEllipsis = () => {
  const Ellipsis = document.createElement("span");
  Ellipsis.classList.add("c-ellipsis");
  Ellipsis.innerText = "...";
  PaginationNumBers.appendChild(Ellipsis);
};

PrevBtn.addEventListener("click", () => {
  if (currentPage > 1) {
    currentPage--;
    CreateNumbers();
  }
});

NextBtn.addEventListener("click", () => {
  if (currentPage < TotalPages) {
    currentPage++;
    CreateNumbers();
  }
});

// 初期表示
CreateNumbers();
